<template>
  <header>
    <!-- <template> -->
      <div class="navbar">
        <div class="logo">DE Circle</div>
        <el-menu
          :default-active="activeIndex2"
          class="menu"
          mode="horizontal"
          @select="handleSelect"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
        >
          <el-menu-item index="1"><router-link to="/">网站首页</router-link></el-menu-item>
          <el-submenu index="2">
            <template slot="title">影视博客</template>
            <el-menu-item index="2-1">电影</el-menu-item>
            <el-menu-item index="2-2">电视剧</el-menu-item>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title">音乐博客</template>
            <el-menu-item index="3-1">欧美音乐</el-menu-item>
            <el-menu-item index="3-2">华语音乐</el-menu-item>
            <el-menu-item index="3-3">粤语音乐</el-menu-item>
            <el-menu-item index="3-4">日韩音乐</el-menu-item>
          </el-submenu>
          <el-submenu index="4">
            <template slot="title">摄影博客</template>
            <el-menu-item index="4-1">地理图片</el-menu-item>
            <el-menu-item index="4-2">人物图片</el-menu-item>
            <el-menu-item index="4-3">植物图片</el-menu-item>
            <el-menu-item index="4-4">风俗图片</el-menu-item>
          </el-submenu>
        </el-menu>
        <div class="search-ct">
          <el-input placeholder="请输入内容" v-model="search" class="input-with-select">
            <el-select v-model="select" slot="prepend" placeholder="请选择">
              <el-option label="电影" value="1"></el-option>
              <el-option label="音乐" value="2"></el-option>
              <el-option label="摄影" value="3"></el-option>
            </el-select>
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </div>
        <div v-if="!isLogin" class="sign">
          <router-link to="/login">
            <el-button :class="[loginClass, {active: isActive}]" type="primary" plain @click="toggle">Sign in</el-button>
          </router-link>
          <router-link to="/register">
            <el-button :class="[registerClass, {active: !isActive}]" type="primary" plain @click="toggle">Sign up</el-button>
          </router-link>
        </div>
        <div v-else class="avatar">
          <router-link to='/my'>
            <el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
          </router-link>
          <ul>
            <li><a href="/my">我的</a></li>
            <li><a href="/">注销</a></li>
          </ul>
        </div>
      </div>
  </header>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data() {
      return {
        isActive: true,
        loginClass: 'login-btn',
        registerClass: 'register-btn',
        search: '',
        select: '',
        activeIndex: ''
      }
    },
    computed: {
      ...mapGetters([
        'isLogin'
      ])
    },
    methods: {
      toggle: function() {
        this.isActive = !this.isActive
      }
    }
}
</script>

<style lang="less">
@import '../assets/common.less';

header {
  background-color: #24292e;
  text-align: center;
  z-index: 2;

  .navbar {
    display: grid;
    grid: auto / 120px 1fr 1fr 200px;
    align-items: center;

    .logo {
      grid-column: 1;
      grid-row: 1;
      width: 120px;
      height: 54px;
      color: #fff;
      font-size: 20px;
      font-weight: bold;
    }

    .logo::before {
      display: inline-block;
      height: 100%;
      content: '';
      vertical-align: middle;
    }

    .menu {
      background-color: #24292e !important;
      border-bottom: 0 !important;

      .el-menu-item, .el-submenu, .el-submenu__title {
        width: 100px;
        height: 54px;
        line-height: 47px;
        font-size: 14px;
        background-color: #24292e !important;
        z-index: 10000 !important;
      }
    }

    .search-ct {
      margin-right: 10px;

      .el-select {
        width: 90px;
        font-size: 12px;
      }

      input {
        height: 35px;
        font-size: 12px;
        outline: none;
      }
    }

    .sign {
      width: 200px;
      display: inline-block;

      button {   // 修改 ui-button 的样式
        margin-left: 5px;
        padding: 8px 10px;
        color: #fff;
        background-color: transparent!important;
        border: none;
      }

      button:hover {
        color: #FFD04B;
        background-color: transparent;
        border: 1px solid #FFD04B;
      }

      .login-btn.active {
        border: 1px solid #FFD04B;
      }
      .register-btn.active {
        border: 1px solid #FFD04B;
      }
    }

    .avatar {
      position: relative;
      margin-left: 50px;
      width: 50px;

      ul {
        display: none;
        position: absolute;
        width: 50px;
        margin: 0;
        padding: 0;
        right: 0;
        border: 1px solid #eaeaea;
        background-color: #fff;

        li a {
          padding: 3px;
          color: #2b2e33;
          font-size: 14px;
        }
      }

      &:hover ul {
        display: block;
      }
    }
  }
}

.el-menu--popup-bottom-start  {
  margin-top: 2px !important;
}
</style>


