import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/index'
import ElementUi from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css';

Vue.config.productionTip = false
Vue.use(ElementUi)

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



