import auth from '../../api/auth'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    login: function() {
      auth.login({ username: this.username, password: this.password })
        .then(res => {
          Message({
            message: '登录成功',
            type: 'success'
          })
        })
    }
  }
}


